// 工具类

export const isMobile = !import.meta.env.SSR ? /Android|webOS|iPhone|iPod|BlackBerry|iPad/i.test(navigator.userAgent) : false;

/**
 * @desc 防抖函数，至少间隔200毫秒执行一次
 *
 * @param {Function} fn callback
 * @param {Number} [ms=200] 默认200毫秒
 * @returns {Function}
 */
export function debounce<C, T extends unknown[]>(fn: (this: C, ...args: T) => void, ms = 200) {
	let timeoutId: NodeJS.Timeout;
	return function (this: C, ...args: T) {
		clearTimeout(timeoutId);
		timeoutId = setTimeout(() => fn.apply(this, args), ms);
	};
}

/**
 * 节流
 * @param fn 回调函数
 * @param delay 时间间隔延迟多少毫秒
 */
export function throttle<C, T extends unknown[]>(fn: (this: C, ...args: T) => void, delay = 200, immediate = false): (this: C, ...args: T) => void {
	let timer: any = null,
		remaining = 0,
		previous = Date.now();
	return function (...args: T) {
		const now = Date.now();
		remaining = now - previous;
		if (remaining >= delay || immediate) {
			if (timer) clearTimeout(timer);
			fn.call(this, ...args);
			previous = now;
			immediate = false;
		} else {
			if (timer) return;
			timer = setTimeout(() => {
				fn.call(this, ...args);
				previous = Date.now();
			}, delay - remaining);
		}
	};
}

export function rafThrottle<T extends (...args: any) => any>(fn: T): T {
	let locked = false;
	return function (this: ThisParameterType<T>, ...args: any[]) {
		if (locked) return;
		locked = true;

		window.requestAnimationFrame(() => {
			Reflect.apply(fn, this, args);
			locked = false;
		});
	} as T;
}

// 对象深度合并
export function deepMerge(obj1: any, obj2: any) {
	let key;
	for (key in obj2) {
		// 如果target(也就是obj1[key])存在，且是对象的话再去调用deepMerge，否则就是obj1[key]里面没这个对象，需要与obj2[key]合并
		// 如果obj2[key]没有值或者值不是对象，此时直接替换obj1[key]
		//console.log(key)
		obj1[key] = obj1[key] && obj1[key].toString() === '[object Object]' && obj2[key] && obj2[key].toString() === '[object Object]' ? deepMerge(obj1[key], obj2[key]) : obj2[key];
	}
	return obj1;
}
